<template>
  <div>
    <v-app-bar elevation="4" color="blue" dark fixed>
      <v-icon @click="$router.go(-1)">chevron_left</v-icon>
      <router-link to="/home">
        <v-img
          src="../assets/e-unsplash-small.jpg"
          height="30"
          width="30"
          max-width="30"
          class="ml-2"
        />
      </router-link>
      <v-icon class="ml-2" @click="dialog = true">map</v-icon>
      <v-spacer></v-spacer>
      <span>{{ title || listPageType }}</span>
      <v-spacer></v-spacer>
      <router-link to="/user-info" class="text-decoration-none">
        <v-icon v-if="showSetting">settings</v-icon>
      </router-link>
    </v-app-bar>
    <div class="d-flex">
      <div
        v-if="showBtn"
        class="d-flex"
        style="margin-top: -10px; margin-right: 5px"
      >
        <v-icon>swap_vert</v-icon>
        <v-icon>filter_alt</v-icon>
      </div>
      <v-text-field
        v-if="showSearch"
        class="mb-3"
        placeholder="請輸入關鍵字"
        v-model="search"
        outlined
        hide-details
        rounded
        dense
        color="blue"
      >
        <template #append>
          <div>
            <v-icon>search</v-icon>
          </div>
        </template>
      </v-text-field>
    </div>
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-app-bar dark color="#2196f3">
          <!-- <v-icon @click="$router.go(-1)">chevron_left</v-icon> -->
          <router-link to="/home">
            <v-img
              src="../assets/e-unsplash-small.jpg"
              height="30"
              width="30"
              max-width="30"
              class="ml-2"
            />
          </router-link>
          <v-icon class="ml-2" @click="dialog = true">map</v-icon>
          <v-spacer></v-spacer>
          <span>{{ title || listPageType }}</span>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-icon dark text @click="setCities"> close </v-icon>
          </v-toolbar-items>
        </v-app-bar>
        <v-container fluid>
          <v-chip-group v-model="selectCities" column multiple>
            <v-chip
              filter
              outlined
              filter-icon="check"
              color="primary"
              v-for="(city, i) in cities"
              :key="i"
            >
              {{ city }}
            </v-chip>
          </v-chip-group>
        </v-container>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  props: {
    showBtn: {
      type: Boolean,
      default: false,
    },
    showSearch: {
      type: Boolean,
      default: true,
    },
    title: {
      type: String,
      default: "",
    },
    showSetting: {
      type: Boolean,
      default: false,
    },
  },

  data: () => ({
    search: "",
    dialog: false,
    cities: [
      "臺北市",
      "新北市",
      "桃園市",
      "臺中市",
      "臺南市",
      "高雄市",
      "新竹市",
      "嘉義市",
      "新竹縣",
      "苗栗縣",
      "彰化縣",
      "南投縣",
      "雲林縣",
      "嘉義縣",
      "屏東縣",
      "宜蘭縣",
      "花蓮縣",
      "臺東縣",
      "澎湖縣",
      "金門縣",
      "連江縣",
      "基隆市",
    ],
    selectCities: [],
  }),

  computed: {
    ...mapState(["listPageType"]),
  },

  mounted() {
    let cities = JSON.parse(localStorage.getItem("selectCities"));
    if (cities && cities.length) {
      this.selectCities = cities;
    }
  },

  methods: {
    setCities() {
      this.dialog = false;
      localStorage.setItem("selectCities", JSON.stringify(this.selectCities));
    },
  },
};
</script>

<style>
.overflow {
  overflow: hidden !important;
}
</style>